<template>
    <div class="supervisionPage">
      <a-card  class="always_shadow" title="关系数据" style="width: 100%">
        <div>
          <a-textarea v-model="desc" placeholder="请输入" :auto-size="{ minRows: 4, maxRows: 5 }" style="width:100%"/>
        </div>
      </a-card>
      <a-row class="mt-4">
        <a-button type="primary" class="rightSetting" @click="onSubmit">提交</a-button>
      </a-row>
      <a-card v-if="keywordAll&&keywordAll[0]" class="always_shadow mt-4" title="实体" style="width: 100%">
        <div>
          <div class="mb-4 pb-4 clearfix">
            <div class="float-left" v-for="(val,index) in keywordAll" :key="index">
              <div class="clearfix mytag">
                <div class="float-left">{{val}}</div>
              </div>
            </div>
          </div>
        </div>
      </a-card>
      <a-card v-if="relationData&&relationData[0]" class="always_shadow mt-4" title="关系" style="width: 100%">
        <div v-for="(item,index) in relationData" :key="index">
          <div v-for="(val,key) in item" :key="key">
            <a-row v-if="val.head" >
              <div class="result_div">
                <span class="highlightA">{{val.head}}</span>
                <span> | </span>
                <span class="highlightB">{{val.tail}}</span>
                <span>的关系</span>
                <span class="highlightC">{{val.relation}}</span>
                <span>的可能性 {{parseFloat(val.score).toFixed(4)}}</span>
              </div>
            </a-row>
          </div>
        </div>
      </a-card>
    </div>
</template>
<script>
  let apiManager='http://192.168.0.254:7777/api/'
  let path = require('path')
    export default {
      name: "supervisionPage",
      data () {
        return {
          relationData: [],
          keywordAll:[],
          desc: '',
        }
      },
      mounted () {
        this.$nextTick( () => {})
      },
      methods:{
        //提交
        onSubmit () {
          let $this=this
          this.$post(apiManager+'sentence',{context:this.desc},function (res) {
            if(!res.relation&&!res.entities){
              $this.$message.warning(res)
            }else{
              $this.keywordAll=res.entities
              if(res.relation&&res.relation[0]){
                $this.relationData=res.relation
              }else{
                $this.relationData=[]
                $this.$message.warning('填写的数据没有关系，请您重新输入查找')
              }
            }
          })
        },
      }
    }
</script>
<style lang="scss" scoped>
  @import "../../assets/css/relationship/supervisionPage.css";
</style>



