<template>
	<div class="documentPage">
		<a-spin :spinning="spinning">
			<div>
				<a-card class="always_shadow" title="文档" style="width: 100%">
					<div>
						<a-upload action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
								  accept=".txt"
								  @change="handleChange"
								  :before-upload="beforeUpload"
						>
							<a-button>
								<a-icon type="upload"/>
								上传txt文本
							</a-button>
						</a-upload>
						<a-textarea class="mt-4" v-model="text" @change="textChange" placeholder="请输入文档"
									:auto-size="{ minRows: 6,maxRows: 10 }" style="width:100%"/>
					</div>
				</a-card>
			</div>
			<div class="mt-4">
				<a-button class="rightSetting" @click="submitsData" type="primary">提交</a-button>
			</div>

			<div class="relative mt-4" v-if="submitChoose" id="container" style="height:500px;width:100%"></div>
			<a-slider id="test" v-if="submitChoose" @change="changeSlider" v-model="slideValue" :min="500" :max="10000"/>
			<div class="mt-4" v-if="keywordAll&&keywordAll[0]">
				<a-card class="always_shadow" title="实体" style="width: 100%">
					<div>
						<div class="border-b border-gray-400 mb-4 pb-4 clearfix">
							<div class="float-left" v-for="(val,index) in keywordAll" :key="index">
								<div class="clearfix" :class="val.existence?'mytag':'mytag1'">
									<div class="float-left">{{val.value}}</div>
								</div>
							</div>
						</div>
					</div>
				</a-card>
				<a-card class="always_shadow" title="关系" style="width: 100%;margin-top: 20px">
					<div>
						<div v-if="relationKeyword&&relationKeyword[0]">
							<a-row v-if="relationKeyword&&relationKeyword[0]">
								<div v-for="(item,index) in relationKeyword" :key="index">
									<div class="result_div">
										<span class="highlightA">{{item.split(',')[0]}}</span>
										<span> => </span>
										<span class="highlightC">{{item.split(',')[1]}}</span>
										<span> => </span>
										<span class="highlightB">{{item.split(',')[2]}}</span>


<!--										<span>的可能性 {{(parseFloat(val.score).toFixed(4))}}</span>-->
									</div>
								</div>
							</a-row>
						</div>
					</div>
				</a-card>
			</div>
		</a-spin>
	</div>
</template>
<script>
	import axios from 'axios'

	let path = require('path')
	import G6 from '@antv/g6'

	let uploadCount = 0
	let graphG6
	let container, widthContainer, heightContainer
	let apiManager = 'http://192.168.0.254:7777/api/'
	export default {
		name: 'documentPage',
		data() {
			return {
				submitChoose: false,
				keyword: '',
				slideValue: 1000,
				zoomOldValue: 1,
				keywordAll: [],
				keywordAllData: [],
				relationKeyword: [],
				typeArr: [
					{
						value: 'ORG', label: 'Organization'
					},
					{
						value: 'LOC', label: 'Location'
					},
					{
						value: 'TIME', label: 'Time'
					},
					{
						value: 'PER', label: 'Person'
					},
					{
						value: 'MISC', label: 'Misc'
					},
					{
						value: 'NUM', label: 'Number'
					}
				],
				type: 'ORG',
				text: '6月23日9时43分，中国在西昌卫星发射中心用长征三号乙运载火箭，成功发射北斗系统第55颗导航卫星。',
				spinning: false
			}
		},
		mounted() {
			this.$nextTick(() => {
			})
		},
		methods: {
			changeSlider() {
				if (graphG6) {
					graphG6.zoom(1 / this.zoomOldValue, { x: widthContainer / 2, y: heightContainer / 2 })
					this.zoomOldValue = this.slideValue / 1000
					let size=parseInt(12 / this.zoomOldValue)
					graphG6.zoom(this.slideValue / 1000, { x: widthContainer / 2, y: heightContainer / 2 })
					graphG6.layout({
						type: 'force',
						nodeSize: size,
						center: [widthContainer / 2, heightContainer / 2],
						preventOverlap: true,
						linkDistance: d => {
							if (d.source.id === 'node0') {
								return 150 / this.zoomOldValue
							}
							return 50  / this.zoomOldValue
						},
						nodeStrength: d => {
							if (d.isLeaf) {
								return -50 / this.zoomOldValue
							}
							return -10 / this.zoomOldValue
						},
						edgeStrength: d => {
							if (d.source.id === 'node1' || d.source.id === 'node2' || d.source.id === 'node3') {
								return 0.7 / this.zoomOldValue
							}
							return 0.1 / this.zoomOldValue
						}
					})
				}
			},
			//当数据变化时改变
			textChange() {
				if (this.keywordAll && this.keywordAll[0]) {
					for (let k in this.keywordAll) {
						if (this.text.indexOf(this.keywordAll[k].value) != -1) {
							this.keywordAll[k].existence = true
						} else {
							this.keywordAll[k].existence = false
						}
					}
				}
			},
			handleChange(file) {
				if (file.file.name.indexOf('.txt') != -1) {
					uploadCount++
					if (uploadCount == 1) {
						const reader = new FileReader()
						reader.readAsText(file.file.originFileObj, 'text/html;charset=utf-8')
						reader.onloadend = () => {
							setTimeout(() => {
								uploadCount = 0
							}, 1000)
							this.text = reader.result
						}
					}
				}
			},
			submitsData() {
				let $this = this
				if (this.text) {
                  this.spinning = true
					this.submitChoose = true
					this.keywordAll = []
					this.relationKeyword = []
					this.$post(apiManager + 'doc', { doc: this.text }, function (res) {
                      $this.spinning = false
                      for (let k in res.entities[0]) {
                          let md = { existence: true, value: res.entities[0][k], type: 'MISC' }
                          $this.keywordAll.push(md)
                      }
                      container = document.getElementById('container')
                      container.innerHTML = ''
                      let dataContainers = {
                          nodes: [],
                          edges: []
                      }
                      $this.relationKeyword = res.relation
						if(res.other_relation){
							if(res.other_relation[0]){
								for(let m=0;m< res.other_relation.length;m++){
									$this.relationKeyword.push(
										res.other_relation[m].subject+','+res.other_relation[m].predicate+','+res.other_relation[m].object
									)
									res.relation.push(res.other_relation[m].subject+','+res.other_relation[m].predicate+','+res.other_relation[m].object)
								}
							}
						}
						function unique (arr) {
							return Array.from(new Set(arr))
						}
						$this.relationKeyword = unique($this.relationKeyword)
						let relation =JSON.parse(JSON.stringify(res.relation))
						relation = unique(relation)
                      if (relation) {
                          for (let k = 0; k < relation.length; k++) {
                            let arr = relation[k].split(',')
                              let dataObject = { id: 'n' + k, label: arr[0],test:arr[2], size: 12 }
                              let dataObject1 = { id: 'nn' + k, label: arr[2],test:arr[0], size: 12 }
                              dataContainers.nodes.push(dataObject)
                              dataContainers.nodes.push(dataObject1)
                              let dataObject2 = {
                                id: 'nnn' + k,
                                label: arr[1],
                                source: 'n' + k,
                                target: 'nn' + k
                              }
                              dataContainers.edges.push(dataObject2)
                          }
                      }
                      let md0 =[]
                      let md1 =[]
                      for(let m in dataContainers.nodes){
                        let cou=0
                        for(let m1 in md0){
                          if(md0[m1].label == dataContainers.nodes[m].label){
                            if(md0[m1].test!=dataContainers.nodes[m].test){
                              md1.push(dataContainers.nodes[m])
                              cou++
                            }
                            break
                          }
                        }
                        if(cou<=0){
                          md0.push(dataContainers.nodes[m])
                        }
                      }
                      for(let m in md1){
                        for(let d in md0){
                          if(md1[m].label ==md0[d].label){
                            for(let i in dataContainers.edges){
                              if(dataContainers.edges[i].target==md1[m].id||dataContainers.edges[i].source==md1[m].id){
                                if(md1[m].test!=md0[d].label){
                                  if(dataContainers.edges[i].target==md1[m].id){
                                    dataContainers.edges[i].target =md0[d].id
                                  }else{
                                    dataContainers.edges[i].source =md0[d].id
                                  }
                                }
                                break
                              }
                            }
                            break
                          }
                        }
                      }
                      dataContainers.nodes=md0
                      const data = dataContainers
                      widthContainer = container.scrollWidth
                      heightContainer = container.scrollHeight || 500
                      const graph = new G6.Graph({
                          container: container,
                          width: widthContainer,
                          height: heightContainer,
                          layout: {
                              type: 'force',
                              center: [widthContainer / 2, heightContainer / 2],
                              preventOverlap: true,
                              linkDistance: d => {
                                  if (d.source.id === 'node0') {
                                      return 100
                                  }
                                  return 50
                              },
                              nodeStrength: d => {
                                  if (d.isLeaf) {
                                      return -50
                                  }
                                  return -10
                              },
                              edgeStrength: d => {
                                  if (d.source.id === 'node1' || d.source.id === 'node2' || d.source.id === 'node3') {
                                      return 0.7
                                  }
                                  return 0.1
                              }
                          },
                          defaultNode: {
                              color: 'rgba(18,120,98,.7)',
                              labelCfg: {
                                  position: 'right',
                                  color: 'rgba(18,120,98,.7)',
                                  fontSize: 12
                              },
                              style: {
                                  lineWidth: 4,
                                  label: 'node(name)',
                                  fill: '#dcfaf3'
                              }
                          },
                          defaultEdge: {
                              size: 1,
                              color: 'rgba(28,238,71,1)',
                              style: {
                                  endArrow: true
                              },
                              labelCfg: {
                                  autoRotate: true,
                                  refY: 10
                              }
                          }
                      })
                      const nodes = data.nodes
                      graph.data({
                          nodes,
                          edges: data.edges.map(function (edge, i) {
                              edge.id = 'edge' + i
                              return Object.assign({}, edge)
                          })
                      })
                      graph.render()
                      graph.on('node:dragstart', function (e) {
                          graph.layout()
                          refreshDragedNodePosition(e)
                      })
                      graph.on('node:drag', function (e) {
                          refreshDragedNodePosition(e)
                      })
                      graph.on('node:dragend', function (e) {
                          e.item.get('model').fx = null
                          e.item.get('model').fy = null
                      })
                      graph.on('node:mouseenter', evt => {
                          const { item } = evt
                          graph.setItemState(item, 'hover', true)
                          graph.updateItem(item, {
                              color: 'rgb(238,93,25)',
                              style: {
                                  lineWidth: 5
                              }
                          })
                      })
                      graph.on('node:mouseleave', evt => {
                          const { item } = evt
                          graph.setItemState(item, 'hover', false)
                          graph.updateItem(item, {
                              color: 'rgba(18,120,98,.7)',
                              style: {
                                  lineWidth: 4
                              }
                          })
                      })
                      graphG6 = graph
                      function refreshDragedNodePosition(e) {
                          const model = e.item.get('model')
                          model.fx = e.x
                          model.fy = e.y
                      }
					})
				} else {
					this.$message.warning('请您输入要查询的文档')
				}
			},
			beforeUpload(file) {
				if (file.name.indexOf('.txt') == -1) {
					this.$message.warning('现只支持.txt文件')
					return false
				} else {
					return true
				}
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import "../../assets/css/relationship/documentPage.css";
</style>



