<template>
	<div class="index">
		<div class="indexTop relative">
			<img class="logoImg" src="../assets/img/Logo_Light.svg" alt="">
			<div class="topHeaderText absolute"><span class="mr-2">·</span>NRE</div>
		</div>
		<div class="relative indexContent">
<!--			<div style="width: 256px" class="absolute top-0 left-0 h-full">-->
<!--				<a-menu style="height:100%"-->
<!--						:default-selected-keys="[currentTab]"-->
<!--						:default-open-keys="['sub1']"-->
<!--						mode="inline" theme="light"-->
<!--						:inline-collapsed="collapsed"-->
<!--						@click="(item) => {this.currentTab = item.key}"-->
<!--				>-->
<!--					<a-menu-item :key="item.key" v-for="item in activeTab">-->
<!--						{{ item.title }}-->
<!--					</a-menu-item>-->
<!--				</a-menu>-->
<!--			</div>-->
			<div class="pageRight">
				<keep-alive>
					<component v-bind:is="currentTabComponent"></component>
				</keep-alive>
			</div>
		</div>
	</div>
</template>
<script>
	import DocumentPage from './relationship/documentPage'
	import SupervisionPage from './relationship/supervisionPage'

	export default {
		name: 'HelloWorld',
		data() {
			return {
				collapsed: false,
				tabs: [{ key: 1, title: '字段关系提取', hidden: true }, { key: 2, title: '文本识别提取' }],
				currentTab: 2
			}
		},
		components: { DocumentPage, SupervisionPage },
		mounted() {
			let firstTab = this.tabs.find(item => !item.hidden)
			this.currentTab = firstTab.key
			this.$nextTick(() => {
			})
		},
		computed: {
			activeTab() {
				return this.tabs.filter(item => !item.hidden)
			},
			currentTabComponent() {
				return this.currentTab === 1 ? SupervisionPage : DocumentPage
			}
		},
		methods: {
			toggleCollapsed() {
				this.collapsed = !this.collapsed
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import "../assets/css/index.css";
</style>



